import React from 'react';
import { Tasks } from './Tasks';
import { FilterDate } from './calendar';
import './Page.css';
import AuthContext from './AuthContext';
import FilterCategory from './Filter'; 
import Form from './Form';
import IF from './Form/component/IF.js';
import {Modal} from './ModalWindow';
import ModalFormDelete from './Form/ModalFormDelete.js';

function formatTwoNumber(number) {
  return (number > 9) ? number : '0'+number;
}

function formatDate(date) {
  var y = date.getFullYear();
  var m = date.getMonth()+1;
  var d = date.getDate();

  return `${y}-${formatTwoNumber(m)}-${formatTwoNumber(d)}`;
}

class PageTask extends React.Component {
  constructor(props) {
    super(props);

    var date = new Date();
    var firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    var lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);

    this.state = {
      isLoadding: false,
      isError: false,
      clients: [],
      tasks: [],
      timers: [],
      filter: {
        date_create:{
          from: formatDate(firstDay),
          to: formatDate(lastDay)
        }
      },
      modal: false,
      modalNew: false,
      modalDelete: false,
      formTask: {},
      modelName: 'task',
      keys: [],
      fields: {}
    }

    this.getTasks = this.getTasks.bind(this);
    this.onUpdate = this.onUpdate.bind(this);
    this.onDel    = this.onDel.bind(this);
    this.onAdd = this.onAdd.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
    this.handleShowTask   = this.handleShowTask.bind(this);
    this.handleChange     = this.handleChange.bind(this);
  }

  componentDidMount() {
    this.context.api.desctiption(this.state.modelName)
    .then(body => this.setState(() => ({
      keys: body.keys || [],
      fields: body.fields || {},
      isLoadding: false,
      isError: false,
    })))
    .catch(err => this.setState(() => ({error: err.message})));
  }

  getTasks(state) {
    if(!state) {
      state = this.state;
    }

    return this.context.api.list(this.state.modelName, state.filter)
    .then(response => {
      return this.setState(() => ({
        ...state,
        tasks: response,
        isLoading: false,
        isError: false
      }));
    })
    .catch(err => 
      this.state.setState(() => ({...state, isError: true, isLoading: false}))
    )
  }

  onUpdate() {
    return this.context.api
    .update(this.state.modelName, this.state.formTask, {
      id: this.state.formTask.id
    })
    .then(() => this.setState(state => ({
      modal: false,
      madalNew: false,
      tasks: state.tasks
        .map(task => (task.id === state.formTask.id)? state.formTask: task)
    })))
    .catch(console.log);
  }

  onDel() {
    return this.context.api
    .del(this.state.modelName, this.state.formTask.id)
    .then(() => this.props.setState(state => ({
      modal: false,
      madalNew: false,
      modalDelete:false,
      tasks: state.tasks.filter(task => task.id !== this.props.formTask.id)
    })))
    .catch(console.log);
  }

  onAdd(set) {
    for(var key in set) {
      if(set[key] === '') {
        return null;
      }
    }
    this.context.api.add(this.state.modelName, set)
    .then(() => this.getTasks({...this.props, modal: false}))
    .catch(console.log);
  }

  handleChange(key, value) {
    console.log(key, value);
    return this.setState(() => ({
      formTask: {
        ...this.state.formTask,
        [key]: value
      } 
    }))
  }

  
  handleShowTask(target, id) {
    if(target.classList.contains('Timer-btn')) {
      return undefined;
    }

    const task = this.state.tasks
      .find(task => task.id === id);

    this.setState(() => ({formTask: task, modal: true, modalNew: false}));
  }

  handleCloseModal() {
    this.setState(() => ({
      modal: false,
      modalNew: false
    }))
  }

  render() {
    console.log(this.state)
    return (
      <div className="App">
        <div className="Page">
          <div className="Header">
            <nav className="navbar navbar-light bg-light">
              <a className="navbar-brand" href="/">Time Tracker</a>
              <div className="btn-group">
                <button className="btn btn-outline-success"
                  type="button"
                  onClick={() => this.setState({
                    modal: true,
                    modalNew: true,
                    formTask: {
                      category_id: this.state.filter.category_id,
                      date_create: new Date().getTime()
                    }
                  })}
                >New Task</button>
                <button className="btn btn-outline-success"
                  type="button"
                  onClick={() => {
                    this.context.api
                    .exportExcel(this.state.modelName, this.state.filter)
                    .then(console.log)
                    .catch(console.log)
                  }}
                >Export excel</button>
                <button className="btn btn-outline-danger"
                  type="button"
                  onClick={() => this.context.signOff()}
                >Sign out</button>
              </div>
            </nav>
          </div>
          <div className="Nav">
            <FilterDate 
              onChange={date_create => 
                this.getTasks({...this.state, filter:{...this.state.filter, date_create}})}
            />
            <FilterCategory
              value={this.state.filter.task_parent}
              modelName="category"
              onChange={(key, value) => 
                this.getTasks({...this.state, filter:{
                  ...this.state.filter,
                  category_id: value 
              }})}
            />
          </div>
          <div className="Article">
            <Tasks
              tasks={this.state.tasks}
              error={this.state.isError}
              loading={this.state.isLoading}
              onShowTask={this.handleShowTask}
            />
          </div>
        </div>
        <IF status={this.state.modalNew && this.state.modal}>
          <Modal show={this.state.modal} onClick={this.handleCloseModal}>
            <h5 key="title" className="modal-title">New task</h5>
            <Form
              row={this.state.formTask}
              keys={this.state.keys}
              fields={this.state.fields}
              onChange={this.handleChange}
            />
            <button key="add" 
              onClick={() => this.onAdd(this.state.formTask)}
              className="btn btn-success"
            >Add</button>
          </Modal>
        </IF>
        <IF status={!this.state.modalNew && this.state.modal}>
          <Modal show={this.state.modal} onClick={this.handleCloseModal}>
            <h5 key="title" className="modal-title">Show task</h5>
            <Form
              row={this.state.formTask}
              keys={this.state.keys}
              fields={this.state.fields}
              onChange={this.handleChange}
            />
            <button key="delete" 
              onClick={() => this.setState(() => ({modalDelete: true}))}
              className="btn btn-danger"
            >Delete</button>,
            <button key="update" 
              onClick={this.onUpdate} 
              className="btn btn-success"
            >Update</button>
          </Modal>
        </IF>
        <IF status={this.state.modalDelete}>
          <ModalFormDelete
            show={this.state.modalDelete}
            modelName={this.props.modelName}
            id={this.state.formTask.id}
            onDelete={this.onDel}
            onClose={() => this.setState(() => ({modalDelete: false}))}
          />
        </IF>
      </div>
    );
  }
}

PageTask.contextType = AuthContext;

export default PageTask;
